header{
    height: 135vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}
/*====CTA=========*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*====Header social=========*/

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    position: absolute;
    left: 0;
    bottom: 10rem;
}
.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}
/*====Me(Image)=========*/

.me{
background:linear-gradient(var(--color-primary),transparent);
width: 25rem;
height: 25rem;
position: absolute;
left:50%;
transform: translateX(-50%);
margin-top: 4rem;
border-radius: 50%;
overflow: hidden;
padding: 1rem 3rem 3rem 4rem;
}

/*======= SCROLL DOWN=========*/

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 15rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: .9rem;
}

/*============MEDIA QUERIES(MEDIUM DEVICE)===========*/
@media screen and (max-width:1024px) {
    header{
        height: 80vh;
    }
}

/*============MEDIA QUERIES(SMALL DEVICE)===========*/
@media screen and (max-width:600px) {
    header{
        height: 100vh;
        
    }
    .header__socials, .scroll__down{
        display: none;
    }

}

